import React, {Component} from 'react';

// Router
import {HashRouter as Router, Route, Switch} from "react-router-dom";

// CSS
import './css/App.css';
import './css/mobile.css';

// Pages
import Home from './pages/Home';
import About from './pages/About';
import Contact from './pages/Contact';
import Shop from './pages/Shop';
import ShopTest from './pages/ShopTest.js';

// Components
import Header from './components/Header'
import TheNav from './components/TheNav'


// App
class App extends Component {
    constructor() {
        super();

        this.state = {
            isCartOpen: false,
            checkout: {
                lineItems: []
            },
            products: [],
            shop: {}
        };

        this.handleCartClose = this
            .handleCartClose
            .bind(this);
        this.addVariantToCart = this
            .addVariantToCart
            .bind(this);
        this.updateQuantityInCart = this
            .updateQuantityInCart
            .bind(this);
        this.removeLineItemInCart = this
            .removeLineItemInCart
            .bind(this);
    }

    componentWillMount() {
        this
            .props
            .client
            .checkout
            .create()
            .then((res) => {
                this.setState({checkout: res});
            });

        this
            .props
            .client
            .product
            .fetchAll()
            .then((res) => {
                this.setState({products: res});
            });

        this
            .props
            .client
            .shop
            .fetchInfo()
            .then((res) => {
                this.setState({shop: res});
            });
    }

    addVariantToCart(variantId, quantity = 1) {
        this.setState({isCartOpen: true});
        const graphqlApiId = window.btoa(`gid://shopify/ProductVariant/${variantId}`)

        const lineItemsToAdd = [
            {
                graphqlApiId,
                quantity
            }
        ]

        const checkoutId = this.state.checkout.id

        return this
            .props
            .client
            .checkout
            .addLineItems(checkoutId, lineItemsToAdd)
            .then(res => {
                this.setState({checkout: res});
            });
    }

    updateQuantityInCart(lineItemId, quantity) {
        const checkoutId = this.state.checkout.id
        const lineItemsToUpdate = [
            {
                id: lineItemId,
                quantity: parseInt(quantity, 10)
            }
        ]

        return this
            .props
            .client
            .checkout
            .updateLineItems(checkoutId, lineItemsToUpdate)
            .then(res => {
                this.setState({checkout: res});
            });
    }

    removeLineItemInCart(lineItemId) {
        const checkoutId = this.state.checkout.id

        return this
            .props
            .client
            .checkout
            .removeLineItems(checkoutId, [lineItemId])
            .then(res => {
                this.setState({checkout: res});
            });
    }

    handleCartClose() {
        this.setState({isCartOpen: false});
    }

    render() {
        return (
            <Router>
                <div>
                    <Header/>
                    <TheNav/>
                    <Switch>
                        <Route exact path="/" component={Home}/>
                        <Route exact path="/about" component={About}/>
                        <Route exact path="/contact" component={Contact}/>
                        <Route exact path="/shop" component={Shop}/>
                        <Route exact path="/shoptest" component={ShopTest}/>
                        <Route
                            render={() => (
                            <div>
                                <h3>404</h3>
                            </div>
                        )}/>
                    </Switch>
                </div>
            </Router>
        );
    }
}

export default App;
